import React, { useEffect } from 'react';
import './index.css';
import image from "./photos/logo.png";
import AI from "./photos/ha.png";
import bicycle from "./photos/bicycle.jpeg";
import mission from "./photos/mission.jpeg";
import { Link} from 'react-router-dom';


function Logo() {
  useEffect(() => {
    // Preload the images
    const images = [image, AI, bicycle, mission];
    images.forEach((src) => {
      const image = new Image();
      image.src = src;
    });
  }, []);

  return (
    <>
      <div className="logo_div">
      <div className="text">
      <p className="color"><span className="color t">AI</span> WITH HEART,
FUTURE WITH
<span className="color t">FANTASY.</span></p>
        <p className="color2">Arborvitae is committed to be a "medicine" for the challenges and
issues our society faces. We believe that technology, specifically
AI, can be harnessed to create lasting and positive change to the
world.</p>

</div>

        <div className="logo_div2">
    
  <div class="circle top-left"></div>
  <div class="circle bottom-left"></div>
  <div class="circle bottom-right"></div>


          
          <div class="ver">
            <p className="detail">
              At Arborvitae, we envision a future where AI
revolutionizes industries, transforms economies, and
improves lives worldwide. Our goal is to drive
innovation and lasting impact by leveraging AI and
machine learning.
            </p>
            <Link to="/contact" className="button">CONTACT US</Link>
          </div>
          <img className="AI_image" src={AI} alt="" />
        </div>
        <img className="bicycle_image" src={bicycle} alt="" />
      </div>
     
    </>
  );
}

export default Logo;

